@import url(https://fonts.googleapis.com/css2?family=Nerko+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nerko+One&family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-image: linear-gradient(120deg, #20e0fa, #d7a8f3);
  color: white;
  height: 100%;
  margin: 0;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

html {
  height: 100%;
}

.landing {
  height: 100vh
}
.navbox {
    display: flex;
    justify-content: flex-end;
    width: 100%;
}

.nav-option { 
    margin: 10px;
    padding: 10px;
    transition: 1s;
}

.option:hover {
    background-color: lightslategray;
    border-radius: 50px;
    transition: 1s;
}

.link {
    font-family: 'Nunito', sans-serif;

    text-decoration: none;
    color: white;
    font-size: large;
}


html{
	scroll-behavior:smooth
}

.landing-box {
    margin-top:15%;
}

.name {
    font-size: 5em;
    font-family: 'Nerko One', cursive;

}

.sub-title {
    font-size: 2em;
    font-family: 'Nerko One', cursive;

}
.skip-button-container {
    margin-top: 20px;
}
.skip-button {
    background-color: rgb(106, 208, 255);
    border-radius: 10px;
   
    padding: 5px;
    font-size: 1.2em;
    font-family: 'Nerko One', cursive;
    text-decoration: none;
    color: white;

    transition-duration: 1s;


}

.skip-button:hover{
    background-color: rgb(17, 180, 255);
}

.section {
    height:100vh
}
.standard-bg {
    background-color: white;
}

.black-header {
    color: black;
    font-size: 3rem;
    padding: 10px;
}

.black-header {
    color: black;
    font-size: 3rem;
    padding: 10px;
    font-family: 'Nerko One', cursive;

}

.black-subheader {
    color: black;
    font-size: 1.5rem;
    padding: 10px;
    font-family: 'Nerko One', cursive;

}

.black-text {
    color: black;
    font-size: 1rem;
    padding: 5px;
    font-family: 'Nunito', sans-serif;

    
}

.tri-flex {
    display: flex;
    justify-content: space-evenly;
}

.preview-image {
    width: 256;
    height: 128;
}

.wrap {
    display: flex;
    flex-wrap: wrap;
}
.footer-section {
    padding-top: 100px;
    background-color: white;
}

.black-text {
    color: black;
    font-size: 1rem;
    padding: 5px;
}

